// Ionic Variables and Theming. For more info, please see:
// http://ionicframework.com/docs/theming/

/** Ionic CSS Variables **/
:root {
  --ion-color-primary: #febe10;
	--ion-color-primary-rgb: 254,190,16;
	--ion-color-primary-contrast: #000000;
	--ion-color-primary-contrast-rgb: 0,0,0;
	--ion-color-primary-shade: #e0a70e;
	--ion-color-primary-tint: #fec528;

	--ion-color-secondary: #002d3d;
	--ion-color-secondary-rgb: 0,45,61;
	--ion-color-secondary-contrast: #ffffff;
	--ion-color-secondary-contrast-rgb: 255,255,255;
	--ion-color-secondary-shade: #002836;
	--ion-color-secondary-tint: #1a4250;

	--ion-color-tertiary: #222428;
	--ion-color-tertiary-rgb: 34,36,40;
	--ion-color-tertiary-contrast: #ffffff;
	--ion-color-tertiary-contrast-rgb: 255,255,255;
	--ion-color-tertiary-shade: #1e2023;
	--ion-color-tertiary-tint: #383a3e;

	--ion-color-success: #539542;
	--ion-color-success-rgb: 83,149,66;
	--ion-color-success-contrast: #ffffff;
	--ion-color-success-contrast-rgb: 255,255,255;
	--ion-color-success-shade: #49833a;
	--ion-color-success-tint: #64a055;

	--ion-color-warning: #494747;
	--ion-color-warning-rgb: 73,71,71;
	--ion-color-warning-contrast: #ffffff;
	--ion-color-warning-contrast-rgb: 255,255,255;
	--ion-color-warning-shade: #403e3e;
	--ion-color-warning-tint: #5b5959;

	--ion-color-danger: #f53d3d;
	--ion-color-danger-rgb: 245,61,61;
	--ion-color-danger-contrast: #ffffff;
	--ion-color-danger-contrast-rgb: 255,255,255;
	--ion-color-danger-shade: #d83636;
	--ion-color-danger-tint: #f65050;

	--ion-color-medium: #6095c9;
	--ion-color-medium-rgb: 96,149,201;
	--ion-color-medium-contrast: #000000;
	--ion-color-medium-contrast-rgb: 0,0,0;
	--ion-color-medium-shade: #5483b1;
	--ion-color-medium-tint: #70a0ce;

	--ion-color-light: #f4f4f4;
	--ion-color-light-rgb: 244,244,244;
	--ion-color-light-contrast: #000000;
	--ion-color-light-contrast-rgb: 0,0,0;
	--ion-color-light-shade: #d7d7d7;
	--ion-color-light-tint: #f5f5f5;

//   --ion-color-white: #ffffff;
 }

