.side_menu {
  background: var(--ion-color-secondary);
  color: white !important;
}
.side_menu .list {
  margin: 10px;
}
.side_menu .item {
  background: var(--ion-color-secondary);
  color: white !important;
  border-bottom: 1px solid white !important;
}

.side_menu button.item:last-child {
  border-bottom: none !important;
  bottom: 0;
  left: 0;
  position: absolute;
  text-align: right;
 
}
