      margin-right: 8px;
    }
  }
}

.alert-wrapper {
  border-radius: 0 !important;
  max-width: 100%;
  width: 272px;
  .alert-head {
    border-top: 4px solid var(--ion-color-primary);
    padding: 0;
    text-align: left;
    .alert-title {
      margin-top: 0;
      padding: 16px 16px 0;
    }
  }
  .alert-message {
    font-size: 1.2rem;
    min-height: 80px;
    padding: 16px !important;
    text-align: left;
    &::-webkit-scrollbar {
      display: none;
      width: 0;
    }
  }
  .alert-button-group {
    border-top: 1px solid #9a9a9a;
    justify-content: flex-end;
    padding: 16px 24px;
    button {
      background: var(--ion-color-primary);
      border: 0;
      border-radius: 4px;
      color: var(--ion-color-secondary);
      font-weight: bold;
      flex: inherit;
      margin: 0 0 0 16px;
      min-width: 60px;
      padding: 8px;
      text-align: left;
      text-transform: capitalize;
      .button-inner {
        font-size: 15px;
        font-weight: bold;
        justify-content: center;
      }
      &:last-child {
        background: transparent;
        border: 2px solid;
      }
    }
  }
}
